import React from "react"
import Layout from "../../components/Layout"
import { graphql, Link } from "gatsby"

export default function Projects({ data }) {
  console.log(data)
  const projects = data.allMarkdownRemark.nodes
  
  return (
    <div>
      <Layout>
        <div className="container mx-auto px-4 md:px-20 pt-28 pb-24">
          <div className="text-5xl font-bold">MY CURRENT PROJECTS</div>
          <div className="text-xl">You can find my personal projects here</div>
          <Link to="./inside-projects"><div className="hover:text-red-400">Click here for more</div></Link>
          <div className="pt-16 grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8">
            {projects.map(project => (
			   
			    <div className="duration-300 rounded-2xl shadow-md items-center hover:shadow-xl">
			      <div className="">
					<img src={ project.frontmatter.thumbnail } alt="C/O https://placeholder.com/" className="rounded-t-xl w-full h-auto" />
				  </div>
			      <div className="p-4">
					<h1 className="text-2xl font-semibold hover:text-red-500"><Link to={"/projects/" + project.frontmatter.slug} key={ project.id }>{ project.frontmatter.title }</Link></h1>
					<p className="text-lg">{ project.frontmatter.stack }</p>
					<a href={ project.frontmatter.live_link } className="inline-block pr-4 hover:text-red-500" target="_blank" rel="noreferrer">ðŸ’» Live Demo</a>
					<a href={ project.frontmatter.code_link } className="inline-block hover:text-red-500" target="_blank" rel="noreferrer">ðŸ“ƒ Source Code</a>
			      </div>
			    </div>
			  
			))}
          </div>
        </div>
      </Layout>
    </div>
  )
}

//export page query
export const query = graphql`
query ProjectPage {
  allMarkdownRemark(
    filter: {fileAbsolutePath: {glob: "**/src/projects/*.md"}}
  ) {
    nodes {
      frontmatter {
        slug
        stack
        title
        thumbnail
        live_link
        code_link
      }
      id
    }
  }
}
`
